 /* // animation */
 
 @keyframes rainbow-color-fill {	
   0% {
     fill: #DAB9FF;
   }	
   20% {
     fill: #FFB9B9;
   }
   40% {
     fill: #FFFEB9;
   }
   60% {
     fill: #B9FFCC;
   }
   80% {
     fill: #B9C9FF;	
   }
   100% {
     fill: #DAB9FF;
   }
 }
 
 @keyframes rainbow-color-stroke {	
   0% {
    stroke: #DAB9FF;
   }	
   20% {
    stroke: #FFB9B9;
 
   }
   40% {
    stroke: #FFFEB9;
   }
   60% {
    stroke: #B9FFCC;
   }
   80% {
    stroke: #B9C9FF;
   }
   100% {
      stroke: #DAB9FF;
   }
 }
 
 .skill-outer {
   outline: none;
 }
 
 .ui {
   &:hover,
   &:focus {
     .circle{
      animation: rainbow-color-fill 1s linear infinite ;
      stroke: transparent;
    }
    /*  */
     .btn,
     text {
       animation: rainbow-color-fill 1s linear infinite ;
       stroke: transparent;
     }
     .heart {
       stroke: transparent;
     }
   }
 }
 
 
 /* /////////////////////  Code  ///////////////////// */
 .code {
   position: relative;
   
   &:hover,
   &:focus {
     .text,
     .rectangle {
       animation: rainbow-color-fill 1s linear infinite ;
       stroke: transparent;
     }
     .change-letter {
       color: #121212;
     }
     .bar {
       stroke: #121212;
     }
     
   }
 }
 
 .change-letter {
   color: #000;
   font-family: roboto;
   font-size: 16px;
   position: absolute;
   text-decoration: none;
   left: calc(50% - 32px);
   top: calc(50% - 20px);
   transform: translate(-50%, -50%);
 
   &:after {
     position: absolute;
     content: "</>";
     animation: changeLetter 3s linear infinite;
     width: 82px;
     text-align: center;
   }
 }
 
 @keyframes changeLetter {
   0%{
     content:"</>";
   }
   20% {
     content:"CSS";
   }
   40% {
     content:"SASS";
   }
   60% {
     content:"JS";
   }
   80%{
     content:"HTML";
   }
   100%{
     content:"</>";
   }
 }
 
