/* 내용 */
:root {
  --surface-color: #eee;
  --curve: 40;
}

* {
  box-sizing: border-box;
}

.cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin: 5rem 5vw;
  list-style-type: none;
}

.card {
  position: relative;
  display: block;
  border-radius: 10px;
  border-radius: calc(var(--curve) * 1px);
  overflow: hidden;
  text-decoration: none;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.2);
}

.card__image {
  margin-top: -5px;
  width: 360px;
  height: 450px;
  box-shadow: inset 10px 10px 10px rgba(0, 0, 0, 0.1);
  
}
.a{
  background: url(../images/HERE.jpg) 0 0 no-repeat;
  background-size: 360px;
}
.b{
  background: url(../images/netfilx.jpg) 0 0 no-repeat;
  background-size: 340px;

}
.c{
  background: url(../images/samsung.jpg) 0 0 no-repeat;
  background-size: 360px;
}
.d{
  background: url(../images/emo.jpg) 0 0 no-repeat;
  background-size: 320px;
}
.e{
  background: url(../images/cjone.jpg) 50% -5px no-repeat;
  background-size: 500px;

}
.f{
  background: url(../images/chat.jpg) 0 0 no-repeat;
  background-size: 310px;
}
.g{
  background: url(../images/kosdak.jpg) 0 0 no-repeat;
  background-size: 340px;
}
.card__overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;      
  border-radius: calc(var(--curve) * 1px);    
  background-color: var(--surface-color);      
  transform: translateY(100%);
  transition: .2s ease-in-out;
}

.card:hover .card__overlay {
  transform: translateY(0);
}

.card__header {
  position: relative;
  display: flex;
  align-items: center;
  gap: 2em;
  padding: 2em;
  border-radius: calc(var(--curve) * 1px) 0 0 0;    
  background-color: var(--surface-color);
  transform: translateY(-100%);
  transition: .2s ease-in-out;
}

.card__arc {
  width: 80px;
  height: 80px;
  position: absolute;
  bottom: 100%;
  right: 0;      
  z-index: 1;
}

.card__arc path {
  fill: var(--surface-color);
  d: path("M 40 80 c 22 0 40 -22 40 -40 v 40 Z");
}       

.card:hover .card__header {
  transform: translateY(0);
}

