
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed:ital@1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed:wght@700&display=swap');

/* Project_header */
.Netflix{
  position: relative;
}
.Netflix_backgrung_img{
  position: absolute;
  z-index: -10;
  top: 180px;
  right:18%;
  width: 330px;
  height: 330px;
  border-radius: 100%;
  background:#ddd;
  opacity: 0.5;
}
.Netflix_Project_info{
  display: inline-block;
  border: 1px solid red;
  position: absolute;
  right: 10%;
  top: 200px;
  /* width: 80%; */
}
.Netflix_Project_title{
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 6em;
  line-height: 5rem;
  color: #000;
  text-align: right;
  border: 1px solid red;
}
.Netflix_title_info{
  /* display: inline-block; */
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1rem;
  text-align: right;
  color: #000;
  border: 1px solid red;
}
/* 이미지 */
.Netflix_img{
  position: relative;
  float: left;
  width: 65%;
  height: 850px;
  margin: 0 auto;
  border: 1px solid blue;
}
.Netflix_t{
  position: absolute;
  top: 0;
  left:0;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  background: url("../images/mockup/NETFLIX_태블릿_MOCKUP.png") 0 0 no-repeat;
  background-size: 100%;
  border: 1px solid blue;
}

/* 내용 */
.Netflix_info{
  position: absolute;
  right: 10%;
  top: 450px;
  font-family: 'Roboto Condensed', sans-serif;
  display: flex;
  flex-direction: column;
  width: 100%;
  border: 1px solid red;
}

/* 이동 */
p.move{
  float: right;
  margin-left: 10px;
  border: 1px solid #555;
  padding: 5px 8px;
  box-sizing: border-box;
  border-radius: 3px;
  color: #555;
}
a{
  font-size: 20px;
  font-weight: normal;
  font-family: 'Roboto', sans-serif;
  color: #555;
  text-decoration: none;
}


/* 프로젝트 요약 내용 */
.Netflix_main{
  font-family: 'Roboto', sans-serif;
  color: #555;
  font-size: 18px;
  border: 1px solid red;
  text-align: right;
}
strong{
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  letter-spacing: 4px;
}

/* 상세내용 */
.Netflix_sub{
  margin-top: 30px;
  font-family: 'Roboto', sans-serif;
  color: #555;
  text-align: right;
  font-size: 18px;
  line-height: 25px;
}
em{
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-size: 18px;
}

