
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed:ital@1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed:wght@700&display=swap');

/* Project_header */
.Samsung{
  position: relative;
}
.Samsung_backgrung_img{
  position: absolute;
  z-index: -10;
  top: 180px;
  right:18%;
  width: 330px;
  height: 330px;
  border-radius: 100%;
  background:#ddd;
  opacity: 0.5;
}
.Samsung_Project_info{
  display: inline-block;
  border: 1px solid red;
  position: absolute;
  right: 10%;
  top: 170px;
  /* width: 80%; */
}
.Samsung_Project_title{
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 6em;
  line-height: 5rem;
  color: #000;
  text-align: right;
  border: 1px solid red;
}
.Samsung_title_info{
  /* display: inline-block; */
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1rem;
  text-align: right;
  color: #000;
  border: 1px solid red;
  
}
/* 이미지 */
.Samsung_img{
  position: relative;
  float: left;
  width: 65%;
  height: 850px;
  margin: 0 auto;
  border: 1px solid blue;
}
.Samsung_t_a{
  position: absolute;
  top: 0;
  left: 5%;
  box-sizing: border-box;
  height: 70%;
  width: 70%;
  background: url("../images/mockup/SAMSUNG_태블릿_sub1_MOCKUP.png") 0 0 no-repeat;
  background-size: 100%;
  border: 1px solid blue;
}
.Samsung_t_b{
  position: absolute;
  top: 150px;
  left: 30%;
  box-sizing: border-box;
  height: 80%;
  width: 90%;
  background: url("../images/mockup/SAMSUNG_태블릿_main_MOCKUP.png") 0 0 no-repeat;
  background-size: 100%;
  border: 1px solid blue;
}
.Samsung_t_c{
  position: absolute;
  top: 520px;
  left: 0;
  box-sizing: border-box;
  height: 70%;
  width: 75%;
  background: url("../images/mockup/SAMSUNG_태블릿_sub2_MOCKUP.png") 0 0 no-repeat;
  background-size: 100%;
  border: 1px solid blue;
}


