/* 내용 */
.Samsung_info{
  position: absolute;
  right: 10%;
  top: 950px;
  font-family: 'Roboto Condensed', sans-serif;
  display: flex;
  flex-direction: column;
  width: 100%;
  border: 1px solid red;
}

/* 이동 */
p.move{
  float: right;
  margin-left: 10px;
  border: 1px solid #555;
  padding: 5px 8px;
  box-sizing: border-box;
  border-radius: 3px;
  >a{
    font-size: 20px;
    font-weight: normal;
    font-family: 'Roboto', sans-serif;
    color: #555;
    text-decoration: none;
  }
}

/* 프로젝트 요약 내용 */
.Samsung_main{
  font-family: 'Roboto', sans-serif;
  color: #555;
  font-size: 18px;
  border: 1px solid red;
  text-align: right;
}
strong{
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  letter-spacing: 4px;
}

/* 상세내용 */
.Samsung_sub{
  font-family: 'Roboto', sans-serif;
  color: #555;
  text-align: right;
  font-size: 18px;
}
em{
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-size: 18px;
}
