
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed:ital@1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed:wght@700&display=swap');

/* Project_header */
.Kosdaq{
  position: relative;
}
.backgrung_Kosdaq_img{
  position: absolute;
  z-index: -10;
  top: -30%;
  left: -20%;
  width: 50%;
  height: 60%;
  border: 10rem solid #ddd;
  opacity: 0.5;

}
.Project_info{
  position: relative;
  left: 10%;
  top: 100px;
}
.Project_title{
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 6em;
  line-height: 5rem;
  color: #000;
  text-align: left;
  border: 1px solid red;
}
.title_info{
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1rem;
  text-align: left;
  color: #000;
  border: 1px solid red;
}
/* 이미지 */
.Kosdaq_img{
  position: relative;
  width: 100%;
  border: 1px solid red;
  height: 850px;
  margin: 0 auto;
}

.Kosdaq_p_a{
  position: absolute;
  right: 0;
  top: -250px;
  box-sizing: border-box;
  height: 100%;
  width: 70%;
  background: url("../images/mockup/KOSDAQ_태블릿_MOCKUP.png") 0 0 no-repeat;
  background-size: 100%;
  border: 1px solid red;
}
.Kosdaq_p_b{
  position: absolute;
  right: 0;
  box-sizing: border-box;
  height: 90%;
  width: 26%;
  background: url("../images/mockup/KOSDAQ_폰_MOCKUP.png") 50% 50% no-repeat;
  background-size: 100%;
  border: 1px solid red;
}


/* 내용 */
.Kosdaq_info{
  position: absolute;
  left: 10%;
  top: 48%;
  font-family: 'Roboto Condensed', sans-serif;
  display: flex;
  flex-direction: column;
  border: 1px solid red;
}

/* 이동 */
p.React_left{
  float: left;
  margin-right: 10px;
  border: 1px solid #555;
  padding: 5px 8px;
  box-sizing: border-box;
  border-radius: 3px;
  >a{
    font-size: 20px;
    font-weight: normal;
    font-family: 'Roboto', sans-serif;
    color: #555;
    text-decoration: none;
  }
}

/* 프로젝트 요약 내용 */
.Kosdaq_main{
  font-family: 'Roboto', sans-serif;
  color: #555;
  font-size: 18px;
  border: 1px solid red;
  text-align: left;
  line-height: 35px;
}
strong{
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  letter-spacing: 4px;
}

/* 상세내용 */
.Kosdaq_sub{
  font-family: 'Roboto', sans-serif;
  color: #555;
  text-align: left;
  font-size: 16px;
}
