
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed:ital@1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed:wght@700&display=swap');

/* Project_header */
.cjone{
  position: relative;
  /* z-index: -1;
  background: webkit-gradient(linear, left top, right bottom, from(#438ee1), to(#973999));
  background: linear-gradient(to bottom right, #FF512F, #DD2476); */
}
.backgrung_cjone_img{
  position: absolute;
  z-index: -10;
  top: -200px;
  left: -15%;
  width: 700px;
  height: 700px;
  border-radius: 100%;
  border: 10rem solid #ddd;
  opacity: 0.5;

}
.Project_cjone_info{
  display: inline-block;
  position: absolute;
  left: 10%;
  top: 100px;
}
.Project_cjone_title{
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 6em;
  line-height: 5rem;
  color: #000;
  text-align: left;
  border: 1px solid red;
}
.cjone_title_info{
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1rem;
  text-align: left;
  color: #000;
  border: 1px solid red;
}
/* 이미지 */
.cjone_img{
  float: left;
  width: 100%;
  border: 1px solid red;
  height: 1000px;
  margin: 0 auto;
}
.cjone_p{
  float: left;
  /* margin-top: 100px; */
  height: 100%;
  width: 30%;
  background: url("../images/mockup/CJ_ONE_폰_MOCKUP.png") 0 100% no-repeat;
  background-size: 100%;
  border: 1px solid red;
}
.cjone_t{
  float: right;
  margin-top: -100px;
  box-sizing: border-box;
  height: 100%;
  width: 70%;
  background: url("../images/mockup/CJ_ONE_태블릿_MOCKUP.png") 0 0 no-repeat;
  background-size: 100%;
  border: 1px solid red;
}
/* detail_plus */
.detail_plus{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background: #fff;
  width: 100%;
  height: 100%;
}

/* 내용 */
.cjone_info{
  position: absolute;
  right: 10%;
  top: 850px;
  font-family: 'Roboto Condensed', sans-serif;
  display: flex;
  margin-bottom: 5%;
  flex-direction: column;
  width: 100%;
  border: 1px solid red;
}

/* 이동 */
p.move{
  float: right;
  margin-left: 10px;
  border: 1px solid #555;
  padding: 5px 8px;
  box-sizing: border-box;
  border-radius: 3px;
}
a{
  font-size: 20px;
  font-weight: normal;
  font-family: 'Roboto', sans-serif;
  color: #555;
  text-decoration: none;
}


/* 프로젝트 요약 내용 */
.main{
  font-family: 'Roboto', sans-serif;
  color: #555;
  font-size: 18px;
  border: 1px solid red;
  text-align: right;
}
strong{
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  letter-spacing: 4px;
}

/* 상세내용 */
.sub{
  font-family: 'Roboto', sans-serif;
  color: #555;
  text-align: right;
  font-size: 18px;
}
em{
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-size: 18px;
}
