
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed:ital@1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed:wght@700&display=swap');

/* Project_header */
.hereapp{
  position: relative;
}
.backgrung_here_img{
  position: absolute;
  z-index: -10;
  top: -25%;
  left: -30%;
  width: 50%;
  height: 60%;
  border: 10rem solid #ddd;
  opacity: 0.5;

}
.Project_here_info{
  display: inline-block;
  position: absolute;
  left: 10%;
  top: 200px;
}
.Project_here_title{
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 6em;
  line-height: 5rem;
  color: #000;
  text-align: left;
  border: 1px solid blue;
}
.here_title_info{

  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1rem;
  text-align: left;
  color: #000;
  border: 1px solid green;
}
/* 이미지 */
.hereapp_img{
  width: 100%;
  /* border: 1px solid red; */
  height: 850px;
  margin: 0 auto;
}

.hereapp_p{
  float: right;
  box-sizing: border-box;
  height: 91%;
  width: 70%;
  background: url("../images/mockup/HERE_APP_폰_MOCKUP.png") 0 0 no-repeat;
  background-size: 100%;
  border: 10px solid #000;
  border-radius: 10px;

}


/* 내용 */
.hereapp_info{
  position: absolute;
  left: 10%;
  top: 85%;
  font-family: 'Roboto Condensed', sans-serif;
  display: flex;
  flex-direction: column;
  /* border: 1px solid red; */
}

/* 이동 */
p.here{
  float: left;
  margin-right: 10px;
  border: 1px solid #555;
  padding: 5px 8px;
  box-sizing: border-box;
  border-radius: 3px;
  >a{
    font-size: 20px;
    font-weight: normal;
    font-family: 'Roboto', sans-serif;
    color: #555;
    text-decoration: none;
  }
}

/* 프로젝트 요약 내용 */
.here_main{
  font-family: 'Roboto', sans-serif;
  color: #555;
  font-size: 18px;
  /* border: 1px solid red; */
  text-align: left;
}
strong{
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  letter-spacing: 4px;
}

/* 상세내용 */
.here_sub{
  font-family: 'Roboto', sans-serif;
  color: #555;
  text-align: left;
  font-size: 16px;
}
