.Project_chat_info{
  display: inline-block;
  position: absolute;
  left: 10%;
  top: 100px;
}
.Project_chat_title{
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 6em;
  line-height: 5rem;
  color: #000;
  text-align: left;
  border: 1px solid red;
}
.title_chat_info{
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1rem;
  text-align: left;
  color: #000;
  border: 1px solid red;
}
/* 이미지 */
.chatapp_img{
  position: relative;
  top: 50px;
  float: right;
  display: flex;
  justify-content: center;
  width: 55%;
  border: 1px solid red;
  height: 700px;
}

.chatapp_p_a{
  height: 100%;
  width: 70%;
  background: url("../images/mockup/CHAT_A_APP_폰_MOCKUP.png") 50% 50% no-repeat;
  background-size: 100%;
  border: 1px solid red;
}
.chatapp_p_b{
  height: 100%;
  width: 70%;
  background: url("../images/mockup/chatting_app_a.png") 50% 50% no-repeat;
  background-size: 80%;
  border: 1px solid red;

}
.chatapp_p_c{
  height: 100%;
  width: 70%;
  background: url("../images/mockup/chatting_app_b.png") 50% 50% no-repeat;
  background-size: 80%;
  border: 1px solid red;
}


/* 내용 */
.chatapp_info{
  position: absolute;
  left: 10%;
  top: 600px;
  font-family: 'Roboto Condensed', sans-serif;
  display: flex;
  flex-direction: column;
  border: 1px solid red;
}

/* 이동 */
p.chat{
  float: left;
  margin-left: 10px;
  border: 1px solid #555;
  padding: 5px 8px;
  box-sizing: border-box;
  border-radius: 3px;
  /* >a{
    font-size: 20px;
    font-weight: normal;
    font-family: 'Roboto', sans-serif;
    color: #555;
    text-decoration: none;
  } */
}

/* 프로젝트 요약 내용 */
.chat_main{
  font-family: 'Roboto', sans-serif;
  color: #555;
  font-size: 18px;
  border: 1px solid red;
  text-align: left;
}
strong{
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  letter-spacing: 4px;
}

/* 상세내용 */
.chat_sub{
  font-family: 'Roboto', sans-serif;
  color: #555;
  text-align: left;
  font-size: 16px;
}
