/* content3 */
.contact{
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 900px;
  align-items: center;
  justify-content: center;
  border: 1px solid red;
  background: blue;
}
.get{
  margin-top: -50px;
  font-family: 'Poiret One', cursive;
  color: #fff;
  font-size: 30px;
  text-align: center;
}
.icon{
  position: absolute;
  right: 300px;
  bottom: 120px;
  background: url(../images/Vector.png) 0 0 no-repeat;
  background-size: 120px;
  width: 165px;
  height: 161px;
}
.mouse {
  border: 1px solid #fff;
  border-radius: 13px;
  top: 150px;
  left: 50%;
  position: absolute;
  display: block;
  height: 46px;
  margin: 0 auto 0 -26px;
  width: 26px;
}
.mouse span {
  animation-duration: 1s;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
  animation-name: scroll;
  background: none repeat scroll 0 0 #fff;
  border: 1px solid transparent;
  border-radius: 4px;
  display: block;
  height: 4px;
  margin: 6px auto;
  width: 4px;
}
@keyframes scroll {
0% {
  opacity: 1;
  transform: translateY(0px);
}
100% {
  opacity: 0;
  transform: translateY(20px);
}
}

.text_cot{
  margin-top: 120px;
  box-sizing: border-box;
  font-family: 'Poller One', cursive;
  color: #fff;
  font-size:130px;
  text-align: center;
}
.st{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: url("../images/PINK.png") 50% 50% no-repeat;
  opacity: 1;
  transition: all 1s ease 0s;
}
.st:hover{
  opacity: 0;
}
.menual{
  padding: 20px 0;
  text-align: center;
  color: #fff;
  font-size: 20px;
}